import React from "react";
import { Link } from "react-router-dom";
import "./Navbar.css"; // 

function Navbar() {
  return (
    <nav className="navbar">
      <Link to="/" className="navbar-link">Home</Link>
      <Link to="/login" className="navbar-link">Login</Link> {/*   */}
    </nav>
  );
}

export default Navbar;
